import { Component } from 'react';
import { Notify } from 'notiflix';

import css from './App.module.css';

import { Searchbar } from './Searchbar/Searchbar';
import { ImageGallery } from './ImageGallery/ImageGallery';
import { Button } from './Button/Button';
import { Loader } from './Loader/Loader';
import { Modal } from './Modal/Modal';
import { fetchPicturesByQuery } from './Api';
// Bez klamr, np. import Searchbar from './Searchbar/Searchbar';, zaimportowana byaby caa zawarto moduu jako pojedyncza zmienna 
// (jeli taka skadnia byaby poprawna w danym kontekcie, co w przypadku komponentw React jest raczej rzadko stosowane)
export class App extends Component {
  constructor() {
    super();
    this.state = {
      images: [],
      query: '',
      page: 1,
      isLoading: false,
      showBtn: false,
      showModal: false,
      largeImageURL: '',
    };
  }
onSubmit = e => {
  e.preventDefault();
  const query = e.target.search.value;

  this.setState({
    query, // Aktualizacja stanu query na warto, ktra zostaa pobrana z formularza 
    isLoading: true,
    images: [],
  });

  this.fetchGallery(query, this.state.page); // wywolanie funkcji
};

onNextPage = () => { // wywoywana po naciniciu Load More
  const { query, page } = this.state;

  this.setState({
    page: page + 1,
    isLoading: true,
  });

  this.fetchGallery(query, page + 1);
};

onClickImage = (url) => { // wywoywana po naciniciu na zdjecie
  this.setState({ showModal: true, largeImageURL: url });
};

onModalClose = () => { // po zamkniciu
  this.setState({ showModal: false, largeImageURL: '' });
};

fetchGallery = (query, page) => { // uywanie fetchP... do pobrania danych
  fetchPicturesByQuery(query, page)
    .then((response) => {
      this.setState((prevState) => ({ // setState jako aktualizacja stanu komponentu
        images: [...prevState.images, ...response], // Aktualizacja images przez dodanie nowych obrazw do istniejcej kolekcji. Operator ... suy do rozpakowywania tablic i umoliwia dodanie nowych elementw do istniejcej tablicy.
        showBtn: response.length === 12,
      }));

      if (response.length === 0) {
        Notify.failure('No matches found!');
      }
    })
    .catch((error) => {
      this.setState({ error });
    })
    .finally(() => { // wykonany niezalenie od tego, czy operacja zakoczya si sukcesem, czy bdem. stan isLoading na false czyli operacja asynchroniczna zostaa zakoczona,
      this.setState({ isLoading: false });
    });
};
    
  render() {
    const { images, isLoading, showBtn, showModal, largeImageURL } = this.state;

    return (
      <div className={css.App}>
        <Searchbar onSubmit={this.onSubmit} />
        <ImageGallery images={images} onClickImage={this.onClickImage} />
        {isLoading && <Loader />} 
            {showBtn && <Button onNextPage={this.onNextPage} />}
        {showModal && (
          <Modal
            largeImageURL={largeImageURL}
            onModalClose={this.onModalClose}
          />
        )}
      </div>
    );
  }
}
// jeli isLoading = true to renderuje sie Loader
// jeli showBtn jest prawdzia to renderuje sie Button z funkcj on NextPage
//  pozwala na dynamiczne dodawanie lub usuwanie komponentu w zalenoci od warunku.